import React,{useEffect,useContext}  from "react"
import {Button,Card,Grid,Input,CardTitle,Select,Error} from "components/common"
import {ThemeContext} from "providers/ThemeProvider"
import {Formik, Field, Form, FieldArray, ErrorMessage} from  "formik"
// import Layout from "../../components/layout"
import * as Yup from "yup"
import {Wrapper,Container,Box} from "./styles"
import TextField from "@material-ui/core/TextField"
import Autocomplete from '@material-ui/lab/Autocomplete';
// import toast from "../../components/decoration/toast"
// import SectionDatabase from "../../database/sectiondatabase"
// import {Error} from "../../components/error/Error"
// import useSWR from "swr"
// import {useRouter} from "next/router"
