//     ()=>import("../../components/myselect/select"),
//         {ssr:false})


let data =  [
    {
        id:1,
        id_:1,
        section:"English section",
        languages:"English"
    },
    {   
        id:2,
        id_:2,
        section:"French section",
        languages:"French"
    },
    {
        id:3,
        id_:3,
        section:"French section",
        languages:"French"
    },
    {
        id:4,
        id_:4,
        section:"French section",
        languages:"French"
    }
]
export const AddClass = ({sectionAll})=>{
    let {theme} = useContext(ThemeContext)
    // const router = useRouter()
    
    
    // let {data , error} = useSWR("/api/sectionapi")
    let sectionOptions = data || sectionAll

    

    // console.log("sectionAll::",sectionAll)
     let initialValues = {
        classes:[{
            id:"",
            clas:"",
            sectionTaught:"",
        }],
     }
    
    let validationSchema = Yup.object({
        classes: Yup.array().nullable().of(
            Yup.object({
                id: Yup.number().nullable().required("enter section ID"),
                clas: Yup.string().nullable().required("enter class").min(3,"least 3 letters"),
                sectionTaught: Yup.string().nullable().required("section Taught")
            })
        )
    })

    let onSubmit = async (values, onSubmitProps) =>{
        onSubmitProps.setSubmitting(true)
        
        // toast.success("BasicConfiguration",{position: toast.POSITION.TOP_CENTER,autoClose:2000})
        {console.log("valuess::",values)}
        let res = await fetch("/api/classesapi",
        {
            method:"POST",
            body: JSON.stringify(values),
            headers:{
                cookies:"name"
            }
        })
        onSubmitProps.resetForm(true)
        console.log("res::",res)
        // if(res.status==400){
        //     router.push("/section/sections")
        // }
        // trigger("/api/a")
    }
    let onReset = ()=>{
        // 
    }

    return (
        <React.Fragment>
            
                <CardTitle >Add New Class</CardTitle>
                     
                <Wrapper >
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={onSubmit}
                        onReset={onReset}
                        >
                        {({
                                values,
                                errors,
                                touched,
                                isValid,
                                dirty,
                                handleChange,
                                handleBlur,
                                handleSubmit,
                                isSubmitting,
                                setFieldValue,
                                getFieldProps
                                /* and other goodies */
                            })=>(
                            <Form onSubmit={handleSubmit}>
                            <Card >
                                
                                
                                        
                                    
                            
                                {/* <CardBody> */}
                                <FieldArray name="classes">
                                    
                                    {   
                                        (fieldprops,index)=>{
                                            let {push,remove,form} = fieldprops
                                            const {values} = form
                                            const {classes} = values 
                                          return(
                                            <div key={index}>
                                            {
                                                classes.map((value,index)=>{
                                                    
                                                    return(
                                                        <div key={index}>
                                                            <Box>
                                                            <Grid theme={theme}>
                                                                <Container>
                                                                    <label  className="" >Section ID</label>
                                                                </Container>
                                                                
                                                                <Container>
                                                                    <Field
                                                                            error={(((errors || {})["classes"] || {})[index] ||{})["id"] && ((((touched || {})["classes"] || {})[index] ||{})["id"])  ? true : false }
                                                                            label={((((errors || {})["classes"] || {})[index] ||{})["id"]) && ((((touched || {})["classes"] || {})[index] ||{})["id"]) ? errors["classes"][index]["id"] : "class id" }
                                                                            {...getFieldProps(classes[index].id)}
                                                                            component={TextField}
                                                                            type="number"
                                                                            step="1"
                                                                            value={value.id}
                                                                            placeholder="class ID"
                                                                            name={`classes[${index}].id`}
                                                                            id={`classes[${index}].id`}
                                                                            min="1"
                                                                            variant="filled"
                                                                        />
                                                                </Container>
                                                                <Container>
                                                                    <label   >Class Name</label>
                                                                </Container>
                                                                
                                                                <Container>
                                                                    <Field
                                                                            error={(((errors || {})["classes"] || {})[index] ||{})["clas"] && ((((touched || {})["classes"] || {})[index] ||{})["clas"])  ? true : false }
                                                                            label={((((errors || {})["classes"] || {})[index] ||{})["clas"]) && ((((touched || {})["classes"] || {})[index] ||{})["clas"]) ? errors["classes"][index]["clas"] : "class" }
                                                                            {...getFieldProps(classes[index].clas)}
                                                                            component={TextField}
                                                                            value={value.clas}
                                                                            placeholder="class"
                                                                            name={`classes[${index}].clas`}
                                                                            id={`classes[${index}].clas`}
                                                                            
                                                                            variant="filled"
                                                                        />
                                                                </Container>
                                                                    <Container>
                                                                        <label  className="" >Section Taught</label>
                                                                    </Container>
                                                                    <div style={{margin:"2% 0 2% 0"}}>
                                                                        <Field
                                                                                
                                                                                component={Autocomplete}
                                                                                // {...getFieldProps("Gender")}
                                                                                value={value.sectionTaught}
                                                                                // defaultValue={values.Title}
                                                                                onBlur={handleBlur}
                                                                                onChange={(e,selectedOption )=>
                                                                                    {setFieldValue(`classes[${index}].sectionTaught`, selectedOption)
                                                                                    //  setFieldError("Gender",errors.Gender)
                                                                                        }
                                                                                }
                                                                                
                                                                                // inputValue={values.Gender.title}
                                                                                options={sectionOptions}
                                                                                name={`classes[${index}].sectionTaught`}
                                                                                id={`classes[${index}].sectionTaught`}
                                                                                getOptionSelected = {(option,valuer)=> option.id_ == valuer.id_}
                                                                                getOptionLabel={option => option.section}
                                                                                renderInput={params => (
                                                                                    <>
                                                                                    
                                                                                    <Field 
                                                                                        component={TextField}
                                                                                        {...params}
                                                                                        name={`classes[${index}].sectionTaught`}
                                                                                        error={(((errors || {})["classes"] || {})[index] ||{})["sectionTaught"] && ((((touched || {})["classes"] || {})[index] ||{})["sectionTaught"])  ? true : false }
                                                                                        label={((((errors || {})["classes"] || {})[index] ||{})["sectionTaught"]) && ((((touched || {})["classes"] || {})[index] ||{})["sectionTaught"]) ? errors["classes"][index]["sectionTaught"] : "section Taught" }
                                                                                        variant="filled"    
                                                                                    
                                                                                    />
                                                                                    </>
                                                                                
                                                                                )}

                            
                                                                            />
                                                                        </div>
                                                                
                                                                            
                                                                <Card theme={theme} scroll="none">
                                                                    <Grid xs={2} md={4}>
                                                                        {index >= 1 ? <Button backgroundColor="red" margin="0 2% 2% 0" onClick={()=>remove(index)}>X</Button> :null}
                                                                    
                                                                        <Button margin="0 2% 2% 0" onClick={
                                                                            ()=>push({
                                                                                    id:"",
                                                                                    clas:"",
                                                                                    sectionTaught:"",
                                                                                })} disabled={!(dirty && isValid ) 
                                                                                }>+</Button>
                                                                    </Grid>
                                                                </Card>
                                                            </Grid>
                                                        </Box>    
                                                        </div>
                                                    )
                                                })
                                            }
                                            
                                            </div>
                                          )  
                                        }
                                    }
                                </FieldArray>
                                {/* </CardBody>  */}
                                <Box>
                                    <div>
                                        
                                        {!(dirty && isValid ) ? <Button margin="0 2% 0 0"  disabled={!(dirty && isValid )} type="submit"  >submit</Button>:<Button margin="0 2% 0 0" type="submit" >submit</Button>}
                                        
                                        <Button type="reset" margin="0 2% 0 0" >reset</Button>
                                    </div> 
                                </Box>
                                 
                               
                            </Card>       
                            </Form>
                            
                        
                            )
}                   
                    </Formik>
                    
                </Wrapper>
                
        </React.Fragment>
    )
}
