import React,{useContext}  from "react"
import {Button, Card,Grid,CardBody,CardTitle} from "components/common"
import {Formik, Field, Form, FieldArray} from  "formik"
import * as Yup from "yup"
// import toast from "../../components/decoration/toast"
import options from "components/common/languages/languages"
import TextField from "@material-ui/core/TextField"
import Autocomplete from '@material-ui/lab/Autocomplete';
// import Select from '@material-ui/core/Select'
// import MenuItem from '@material-ui/core/MenuItem';
// import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
// import DoneIcon from '@material-ui/icons/Done';
// import InputAdornment from "@material-ui/core/InputAdornment"
// import Button from "@material-ui/core/Button"

import {Wrapper,Box,Container} from "./styles"
import { ThemeContext } from 'providers/ThemeProvider';

export const Section = ()=>{

    const { theme } = useContext(ThemeContext);

     let initialValues = {
        
        section:[{
            id:"",
            sectionName:"",
            sectionLanguage:"",
        }],
     }
    
    let validationSchema = Yup.object({
        section: Yup.array().nullable().of(
            Yup.object({
                id: Yup.number().nullable().required("enter section ID"),
                sectionName: Yup.string().nullable().required("enter section").min(3,"Greater than 3 letters"),
                sectionLanguage: Yup.string().nullable().required("enter Language")
            })
        )
    })
    let onSubmit = async (values, onSubmitProps) =>{
        onSubmitProps.setSubmitting(true)
        // toast.success("BasicConfiguration",{position: toast.POSITION.TOP_CENTER,autoClose:2000})
        
        let res = await fetch("/api/sectionapi",
        {
            method:"POST",
            body: JSON.stringify(values),
            headers:{
                cookies:"name"
            }
        })
        onSubmitProps.resetForm(true)
        // trigger("/api/a")
    }
    let onReset = ()=>{
        // 
    }

    return (
        
            <Wrapper >
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={onSubmit}
                        onReset={onReset}
                        >
                        {({
                                
                                errors,
                                touched,
                                isValid,
                                dirty,
                                handleChange,
                                handleBlur,
                                handleSubmit,
                                isSubmitting,
                                setFieldValue,
                                getFieldProps
                                /* and other goodies */
                            })=>(
                            <Form onSubmit={handleSubmit} >
                                <Card theme={theme} scroll="none">
                                    <CardTitle theme={theme}>
                                       Add New Section 
                                    </CardTitle>
                                    
                                
                                    <CardBody theme={theme} scroll="none">
                                    {/* <Grid xs={4} margin="auto 24px">
                                        <label  className="" >Section ID</label>
                                        <label  className="" >Section Name</label>
                                        <label  className="" >Section Language</label>
                                        <label  className="d-none d-lg-block" >del</label>
                                        <label  className="d-none d-lg-block" >add</label>
                                    </Grid> */}
                                    <FieldArray name="section">
                                        {   
                                            (fieldprops,index)=>{
                                                let {push,remove,form} = fieldprops
                                                const {values} = form
                                                const {section} = values 
                                            return(
                                                <div key={index}>
                                                {
                                                    section.map((value,index)=>{
                                                        return(
                                                            <div key={index}>
                                                                <Box>
                                                                    <Grid  theme={theme} >
                                                                        

                                                                        
                                                                        {/* <div > */}
                                                                        <Container>
                                                                            <label  className="" >Section ID</label>
                                                                        </Container>
                                                                        
                                                                        <Container>
                                                                            <Field
                                                                                    error={(((errors || {})["section"] || {})[index] ||{})["id"] && ((((touched || {})["section"] || {})[index] ||{})["id"])  ? true : false }
                                                                                    label={((((errors || {})["section"] || {})[index] ||{})["id"]) && ((((touched || {})["section"] || {})[index] ||{})["id"]) ? errors["section"][index]["id"] : "section" }
                                                                                    {...getFieldProps(section[index].id)}
                                                                                    component={TextField}
                                                                                    type="number"
                                                                                    step="1"
                                                                                    value={value.id}
                                                                                    placeholder="Section ID"
                                                                                    name={`section[${index}].id`}
                                                                                    id={`section[${index}].id`}
                                                                                    min="1"
                                                                                    variant="filled"
                                                                                />
                                                                        </Container>
                                                                                
                                                                        {/* </div> */}
                                                                        {/* <div> */}
                                                                        <Container>
                                                                            <label  className="" >Section Name</label>
                                                                        </Container>
                                                                        <Container>
                                                                            <Field
                                                                                    
                                                                                error={errors.hasOwnProperty(`section[${index}].sectionName`) && touched.hasOwnProperty(`section[${index}].sectionName`) ? true : false }
                                                                                {...getFieldProps(section[index].sectionName)}
                                                                                value={value.sectionName}
                                                                                component={TextField}
                                                                                placeholder="Section Name"
                                                                                name={`section[${index}].sectionName`}
                                                                                id={`section[${index}].sectionName`}
                                                                                variant="filled"
                                                                            />
                                                                        </Container>
                                                                        <Container>
                                                                            <label  className="" >Section Language</label>
                                                                        </Container>
                                                                        {/* <Container> */}
                                                                        <div style={{margin:"2% 0 2% 0"}}>
                                                                        <Field
                                                                                
                                                                                component={Autocomplete}
                                                                                // {...getFieldProps("Gender")}
                                                                                value={value.sectionLanguage}
                                                                                // defaultValue={values.Title}
                                                                                onBlur={handleBlur}
                                                                                onChange={(e,selectedOption )=>
                                                                                    {setFieldValue(`section[${index}].sectionLanguage`, selectedOption)
                                                                                    //  setFieldError("Gender",errors.Gender)
                                                                                        }
                                                                                }
                                                                                
                                                                                // inputValue={values.Gender.title}
                                                                                options={options}
                                                                                name={`section[${index}].sectionLanguage`}
                                                                                id={`section[${index}].sectionLanguage`}
                                                                                getOptionSelected = {(option,valuer)=> option.value == valuer.value}
                                                                                getOptionLabel={option => option.value}
                                                                                renderInput={params => (
                                                                                    <>
                                                                                    
                                                                                    <Field 
                                                                                        component={TextField}
                                                                                        {...params}
                                                                                        name={`section[${index}].sectionLanguage`}
                                                                                        error={(((errors || {})["section"] || {})[index] ||{})["sectionLanguage"] && ((((touched || {})["section"] || {})[index] ||{})["sectionLanguage"])  ? true : false }
                                                                                        label={((((errors || {})["section"] || {})[index] ||{})["sectionLanguage"]) && ((((touched || {})["section"] || {})[index] ||{})["sectionLanguage"]) ? errors["section"][index]["sectionLanguage"] : "sectionLanguage" }
                                                                                        variant="filled"    
                                                                                    
                                                                                    />
                                                                                    </>
                                                                                
                                                                                )}

                            
                                                                            />
                                                                        </div>
                    
                                                                        {/* <Card theme={theme} scroll="none"> */}
                                                                            <Grid xs={2} md={4}>
                                                                                {index >= 1 ? <Button  backgroundColor="red" margin="0 2% 2% 0"  type="button" onClick={()=>remove(index)}>X</Button> :null}
                                                                                <Button margin="0 2% 2% 0"  type="button" onClick={
                                                                                    ()=>push({
                                                                                            id:"",
                                                                                            sectionName:"",
                                                                                            sectionLanguage:"",
                                                                                        })} disabled={!(dirty && isValid )
                                                                                    }>
                                                                                        +
                                                                                </Button>
                                                                            </Grid>
                                                                        {/* </Card> */}
                                                                        
                                                                    </Grid>
                                                                </Box>
                                                            </div>
                                                        )
                                                    })
                                                }
                                                
                                                </div>
                                            )  
                                            }
                                        }
                                    </FieldArray>
                                    </CardBody>
                                    <Box>
                                        <Grid  xs={2} md={4} sm={2} lg={4} >
                                            
                                            {!(dirty && isValid ) ? <Button margin="0 2% 0 0"  disabled={!(dirty && isValid )} type="submit"  >submit</Button>:<Button margin="0 2% 0 0"   type="submit"  >submit</Button>}
                                            
                                            <Button as="button" type="reset" margin="0 2% 0 0" backgroundColor="#ebdd25">reset</Button>
                                        </Grid>
                                    </Box>
                                    
                                </Card>       
                            </Form>
                            )
}                   
                    </Formik>
            </Wrapper>
        
    )
}


