import React,{useContext}  from "react"
import {Formik, Field, Form, FieldArray} from  "formik"
import {Grid} from "components/common"
import * as Yup from "yup"
import TextField from "@material-ui/core/TextField"
// import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
// import DoneIcon from '@material-ui/icons/Done';
import InputAdornment from "@material-ui/core/InputAdornment"
import Button from "@material-ui/core/Button"
import { Wrapper,Box,Box_space } from "./styles"
import {ThemeContext} from "providers/ThemeProvider"


export default  ({property})=>{
    let {theme} = useContext(ThemeContext)
    
     let initialValues = {
        
        [property.property]:[{
            [property.propertyName]:""
        }],
     }
    
    let validationSchema = Yup.object({
        [property.property]: Yup.array().nullable().of(
            Yup.object({
                [property.propertyName]: Yup.string().nullable().required(property.error),
            })
        )
    })
    let onSubmit = async (values, onSubmitProps) =>{
        console.log("submittinProps",onSubmitProps)
        onSubmitProps.setSubmitting(true)
        
        
        
        let res = await fetch(`/api/generalapi?${property.property}=true`,
        {
            method:"POST",
            body: JSON.stringify(values),
            headers:{
                cookies:"name"
            }
        })
        // toast.success("BasicConfiguration",{position: toast.POSITION.TOP_CENTER,autoClose:2000})
        onSubmitProps.resetForm(true)

        // onSubmitProps.setErrors((errors)=>{
        //     errors = {}
        //     return errors
        // })

        // onSubmitProps.resetForm(true)
        // trigger("/api/a")
    }
    let onReset = ()=>{
        // 
    }

    return (
        <React.Fragment>
            
                <Wrapper fluid="true" className="p-3 opacityControl" theme={theme}>
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={onSubmit}
                        onReset={onReset}
                        >
                        {({
                                isValid,
                                dirty,
                                handleSubmit,
                                errors,
                                isSubmitting,
                                setFieldValue,
                                getFieldProps,
                                touched
                                /* and other goodies */
                            })=>(
                            <Form onSubmit={handleSubmit}>
                            {/* <Card theme={theme}> */}
                        
                                <div className="mt-3 mb-4 ml-3">
                                    <h3>
                                        {property.property}
                                    </h3>
                                </div>
                            
                                {/* <CardBody  theme={theme}> */}
                                <FieldArray name={property.property}>
                                    
                                    {   
                                        (fieldprops,index)=>{
                                            let {push,remove,form} = fieldprops
                                            const {values} = form
                                            // [property.property]
                                            const position = values[property.property]
                                          return(
                                            <div key={index}>
                                            {
                                                position.map((value,index)=>{
                                                    return(
                                                        <Box key={index}>
                                                            <Grid theme={theme}>
                                                                    {/* <label  className="" >{property.property}</label> */}
                                                                    <Field
                                                                        error={(((errors || {})[property.property] || {})[index] ||{})[property.propertyName] && ((((touched || {})[property.property] || {})[index] ||{})[property.propertyName])  ? true : false }
                                                                        label={((((errors || {})[property.property] || {})[index] ||{})[property.propertyName]) && ((((touched || {})[property.property] || {})[index] ||{})[property.propertyName]) ? errors[property.property][index][property.propertyName] : `${property.property}` }
                                                                        {...getFieldProps((property.property)[index][property.propertyName])}
                                                                        variant="filled"
                                                                        value={value[property.propertyName]}
                                                                        component={TextField}
                                                                        name={`${property.property}[${index}][${property.propertyName}]`}
                                                                        id={`${property.property}[${index}][${property.propertyName}]`}
                                                                        
                                                                        InputProps={{
                                                                            endAdornment:(
                                                                                <InputAdornment position="start" >
                                                                                    {index >= 1 ? <Button   color="secondary" onClick={()=>remove(index)}>x</Button> :null}
                                                                                    <Button  color="primary"
                                                                                      onClick={
                                                                                        ()=>push({
                                                                                                [property.propertyName]:"",
                                                                                            })} disabled={!(dirty && isValid ) 
                                                                                    }>&#10003;</Button>
                                                                                </InputAdornment>
                                                                            )
                                                                        }}
                                                                    />
                                                                    
                                                            </Grid>
                                                            
                                                        </Box>
                                                    )
                                                })
                                            }
                                            
                                            </div>
                                          )  
                                        }
                                    }
                                </FieldArray>
                                {/* </CardBody>  */}
                                
                                <div>
                                    <Box_space>
                                        
                                        {!(dirty && isValid ) ? <Button  variant="contained" disabled={!(dirty && isValid )} type="submit"  >submit</Button>:<Button color="primary" variant="contained"  type="submit"  >submit</Button>}
                                    </Box_space>   
                                    <Box_space> 
                                        <Button color="secondary" variant="contained" type="reset"  >reset</Button>
                                    </Box_space> 
                                </div>
                                 
                               
                            {/* </Card>        */}
                            </Form>
                            
                        
                            )
}                   
                    </Formik>
                    
                </Wrapper>
                
            
        </React.Fragment>
    )
}

