let sectionOptions =  [
    {
        id_:1,
        section:"English section",
        languages:"English"
    },
    {
        id_:2,
        section:"French section",
        languages:"French"
    },
    {
        id_:2,
        section:"French section",
        languages:"French"
    },
    {
        id_:2,
        section:"French section",
        languages:"French"
    }
]

let AddSubjects = ({sectionAll})=>{
    // const router = useRouter()
    let {theme} = useContext(ThemeContext)
    
    
    
    // let {data , error} = useSWR("/api/sectionapi")
    // let sectionOptions = data || sectionAll
    // console.log("sectionAll::",sectionAll)
     let initialValues = {
        
        subject:[{
            id:"",
            subjectName:"",
            sectionTaught:"",
        }],
     }
    
    let validationSchema = Yup.object({
        subject: Yup.array().nullable().of(
            Yup.object({
                id: Yup.number().nullable().required("enter section ID").min(3,"least three digits"),
                subjectName: Yup.string().nullable().required("enter section").min(3,"least 3 letters"),
                sectionTaught: Yup.string().nullable().required("enter Language")
            })
        )
    })
    let onSubmit = async (values, onSubmitProps) =>{
        onSubmitProps.setSubmitting(true)
        
        // toast.success("BasicConfiguration",{position: toast.POSITION.TOP_CENTER,autoClose:2000})
        // {console.log("valuess::",values)}
        let res = await fetch("/api/subjectapi",
        {
            method:"POST",
            body: JSON.stringify(values),
            headers:{
                cookies:"name"
            }
        })
        onSubmitProps.resetForm(true)
        console.log("res::",res)
        if(res.status===400){
            // router.push("/section/sections")
        }
        // trigger("/api/a")
    }
    let onReset = ()=>{
        // 
    }

    return (
        <React.Fragment>
                <Wrapper>
                    <CardTitle >
                        Add New Subject
                    </CardTitle>
                    <Box>
                        <Formik
                            initialValues={initialValues}
                            validationSchema={validationSchema}
                            onSubmit={onSubmit}
                            onReset={onReset}
                            >
                            {({
                                    values,
                                    errors,
                                    touched,
                                    isValid,
                                    dirty,
                                    handleChange,
                                    handleBlur,
                                    handleSubmit,
                                    isSubmitting,
                                    setFieldValue,
                                    getFieldProps
                                    /* and other goodies */
                                })=>(
                                <Form onSubmit={handleSubmit}>
                                <Card >

                                    <FieldArray name="subject">
                                        
                                        {   
                                            (fieldprops,index)=>{
                                                let {push,remove,form} = fieldprops
                                                const {values} = form
                                                const {subject} = values 
                                            return(
                                                <div key={index}>
                                                {
                                                    subject.map((value,index)=>{
                                                        
                                                        return(
                                                            <div key={index}>
                                                                <Grid>
                                                                    <Container>
                                                                        <label  className="" >Class ID</label>
                                                                    </Container>
                                                                    
                                                                    <Container>
                                                                        <Field
                                                                                error={(((errors || {})["subject"] || {})[index] ||{})["id"] && ((((touched || {})["subject"] || {})[index] ||{})["id"])  ? true : false }
                                                                                label={((((errors || {})["subject"] || {})[index] ||{})["id"]) && ((((touched || {})["subject"] || {})[index] ||{})["id"]) ? errors["subject"][index]["id"] : "class id" }
                                                                                {...getFieldProps(subject[index].id)}
                                                                                component={TextField}
                                                                                type="number"
                                                                                step="1"
                                                                                value={value.id}
                                                                                placeholder="Subject ID"
                                                                                name={`subject[${index}].id`}
                                                                                id={`subject[${index}].id`}
                                                                                min="1"
                                                                                variant="filled"
                                                                            />
                                                                    </Container>
                                                                        <Container>
                                                                        <label   >Subject Name</label>
                                                                        </Container>
                                                                        
                                                                        <Container>
                                                                            <Field
                                                                                    error={(((errors || {})["subject"] || {})[index] ||{})["subjectName"] && ((((touched || {})["subject"] || {})[index] ||{})["subjectName"])  ? true : false }
                                                                                    label={((((errors || {})["subject"] || {})[index] ||{})["subjectName"]) && ((((touched || {})["subject"] || {})[index] ||{})["subjectName"]) ? errors["subject"][index]["subjectName"] : "subject Name" }
                                                                                    {...getFieldProps(subject[index].subjectName)}
                                                                                    component={TextField}
                                                                                    value={value.clas}
                                                                                    placeholder="subject Name"
                                                                                    name={`suject[${index}].subjectName`}
                                                                                    id={`subject[${index}].subjectName`}
                                                                                    
                                                                                    variant="filled"
                                                                                />
                                                                        </Container>
                                                                        <Container>
                                                                            Section Taught
                                                                        </Container>
                                                                        <div style={{margin:"2% 0 2% 0"}}>
                                                                            <Field
                                                                                component={Autocomplete}
                                                                                // {...getFieldProps("Gender")}
                                                                                value={value.sectionTaught}
                                                                                // defaultValue={values.Title}
                                                                                onBlur={handleBlur}
                                                                                onChange={(e,selectedOption )=>
                                                                                    {setFieldValue(`subject[${index}].sectionTaught`, selectedOption)
                                                                                    //  setFieldError("Gender",errors.Gender)
                                                                                        }
                                                                                }
                                                                                
                                                                                // inputValue={values.Gender.title}
                                                                                options={sectionOptions}
                                                                                name={`subject[${index}].sectionTaught`}
                                                                                id={`subject[${index}].sectionTaught`}
                                                                                getOptionSelected = {(option,valuer)=> option.id_ == valuer.id_}
                                                                                getOptionLabel={option => option.section}
                                                                                renderInput={params => (
                                                                                    <>
                                                                                    
                                                                                    <Field 
                                                                                        component={TextField}
                                                                                        {...params}
                                                                                        name={`subject[${index}].sectionTaught`}
                                                                                        error={(((errors || {})["subject"] || {})[index] ||{})["sectionTaught"] && ((((touched || {})["subject"] || {})[index] ||{})["sectionTaught"])  ? true : false }
                                                                                        label={((((errors || {})["subject"] || {})[index] ||{})["sectionTaught"]) && ((((touched || {})["subject"] || {})[index] ||{})["sectionTaught"]) ? errors["subject"][index]["sectionTaught"] : "section Taught" }
                                                                                        variant="filled"    
                                                                                    
                                                                                    />
                                                                                    </>
                                                                                
                                                                                )}

                            
                                                                            />
                                                                        </div>
                                                                        
                                                                        
                                                                    
                                                                                
                                                                
                                                                    <Card theme={theme} scroll="none">
                                                                        <Grid xs={2} md={4}>
                                                                            {index >= 1 ? <Button backgroundColor="red" margin="0 2% 0 0" type="Button" onClick={()=>remove(index)}>X</Button> :null}
                                                                        
                                                                            <Button margin="0 2% 0 0"  type="Button" onClick={
                                                                                ()=>push({
                                                                                        id:"",
                                                                                        subjectName:"",
                                                                                        sectionTaught:"",
                                                                                    })} disabled={!(dirty && isValid || isSubmitting) 
                                                                                    }>+</Button>
                                                                        </Grid>
                                                                    </Card>
                                                                    
                                                                </Grid>
                                                                
                                                            </div>
                                                        )
                                                    })
                                                }
                                                
                                                </div>
                                            )  
                                            }
                                        }
                                    </FieldArray>
                                    
                                    <div>
                                        
                                        {!(dirty && isValid || isSubmitting) ? <Button margin="2% 2% 0 0"  disabled={!(dirty && isValid || isSubmitting)} type="submit">submit</Button>:<Button margin="2% 2% 0 0" as="Button"  type="submit">submit</Button>}
                                        
                                        <Button margin="2% 2% 0 0" backgroundColor="yellow" type="reset" className="pl-5 pr-5 m-2 bg-warning" >reset</Button>
                                    </div>  
                                
                                </Card>       
                                </Form>
                                
                            
                                )
    }                   
                        </Formik>
                    </Box>    
                </Wrapper>
        </React.Fragment>
    )
}


