import React , {useState,useEffect} from "react"
import {Button,Select, Table,TableBody,TableData,TableRow,Input} from "components/common"
// import useSWR from "swr"
// import toast from "../../components/decoration/toast"
import {BsXOctagonFill,BsPen} from "react-icons/bs"
import {Formik,Form, FastField} from "formik"
import options from "components/common/languages/languages"
import {Wrappper} from "./styles"

// import SectionDatabase from "../../database/sectiondatabase"

// let BrowserSiteOutput = dynamic(
//     ()=>import("../../components/browserSiteOutput").then((mod)=>mod.BrowserSiteOutput),
//         {ssr:false})

let AllSection = ({totalsections})=>{
    let property = [
        "id",
        "section",
        "language",
        "Modifier"
    ]

    // let {data, error} = useSWR('/api/sectionapi')
    let data =  [
        {
            id:1,
            id_:1,
            section:"English section",
            languages:"English"
        },
        {   
            id:2,
            id_:2,
            section:"French section",
            languages:"French"
        },
        {
            id:3,
            id_:3,
            section:"French section",
            languages:"French"
        },
        {
            id:4,
            id_:4,
            section:"French section",
            languages:"French"
        }
    ]
    // console.log("Sectiondata::",data)
    // data = data || totalsections
    // console.log("Sectiondata2::",data[1])
    let stater = (data)=>{
        let state = {}
        if(data){
            for(let d of data){
                state[`displayon${d.id || d[property[1]]}`]="table-row"
                state[`displayoff${d.id || d[property[1]]}`]="none"
            }
            console.log("state::",state)
            return state
        }
        return null
    }
    let [initialState, setInitialState] = useState(null)
    let [initialdisplay, setInitialdisplay] = useState(stater(data))
    
    
    useEffect(()=>{
        setInitialdisplay((previousState)=>({
            
            ...stater(data),
            ...previousState,
        }))
    },[data])
       
    let initialFormValue=[]
    let onsubmitForm = (values,submittingProps)=>{
        
    }
    return (
        <React.Fragment>
            {/* <Layout> */}
                <Wrappper>
                    {/* <h3 className="bg-light rounded">Section</h3> */}
                    {/* <BrowserSiteOutput /> */}
                        <Formik
                            initialValues={initialState || initialFormValue}
                            onSubmit={onsubmitForm}
                            enableReinitialize
                            >
                                {
                                    ({handleBlur,handleChange,handleSubmit,values,getFieldProps})=>{
                                        return(
                                            <Form onSubmit={handleSubmit}>
                                                <Table responsive >
                                                    
                                                    <TableRow>
                                                        {/* <TableHeader>  */}
                                                        {
                                                            property.map((value,index)=>{
                                                                return(
                                                                    <TableData key={value+String(index)}>{value}</TableData>
                                                                )
                                                            })
                                                        }
                                                            
                                                           
                                                        {/* </TableHeader> */}
                                                    </TableRow>
                                                
                                                    <TableBody>
                                                        {
                                                            data ? data.map((value,index)=>{
                                                                return (
                                                                    <React.Fragment key={value.id_}>
                                                                        <TableRow style={{display:initialdisplay[`displayon${value.id_}`]}}>
                                                                            <TableData >{value.id_}</TableData>
                                                                            <TableData>{value.section}</TableData>
                                                                            <TableData>{value.languages}</TableData>
                                                                            <TableData>
                                                                                
                                                                                    <Button width="fit-content" margin="2px" padding="0px" onClick = {()=>{
                                                                                        
                                                                                                    setInitialdisplay((previousState)=>({
                                                                                                        ...previousState,
                                                                                                        
                                                                                                        [`displayon${value.id_}`]:"none",
                                                                                                        [`displayoff${value.id_}`]:"table-row"
                                                                                                    
                                                                                                }))
                                                                                                    setInitialState((previousState)=>({
                                                                                                            ...previousState,
                                                                                                            [`section${value.id_}`]:{
                                                                                                                id_ : value.id_,
                                                                                                                section:value.section,
                                                                                                                languages:value.languages
                                                                                                            },
                                                                                                        
                                                                                                    }))
                                                                                    }} 
                                                                                    ><BsPen color="black" />
                                                                                        
                                                                                    </Button>
                                                                                
                                                                                    <Button width="fit-content" margin="2px" padding="0px" type="button"  onClick={async ()=>{
                                                                                            let responce = await fetch("/api/sectionapi",{
                                                                                                method:"DELETE",
                                                                                                body:JSON.stringify({
                                                                                                    section:{
                                                                                                        id_:value.id_,
                                                                                                        languages:value.languages,
                                                                                                        section:value.section
                                                                                                    }
                                                                                                })
                                                                                            })
                                                                                            // await toast.success(`${JSON.stringify(responce)} was deleted`,{position: toast.POSITION.TOP_CENTER,autoClose:2000})
                                                                                        }}><BsXOctagonFill color="black" className="m-2"/>
                                                                                            
                                                                                </Button>
                                                                            </TableData>
                                                                            
                                                                        </TableRow>
                                                                        <TableRow style={{display:initialdisplay[`displayoff${value.id_}`]}}>
                                                                            <TableData>
                                                                            <FastField 
                                                                                as={Input}
                                                                                {...getFieldProps(`section${value.id_}.id_`)}
                                                                                name={`section${value.id_}.id_`}
                                                                                readOnly = {true}
                                                                                value={value.id_}
                                                                                type="number"
                                                                                min="1"
                                                                                step="1"
                                                                                />
                                                                            </TableData>
                                                                            <TableData>
                                                                                <FastField 
                                                                                    {...getFieldProps(`section${value.id_}.section`)}
                                                                                    name={`section${value.id_}.section`}
                                                                                    as={Input}
        
                                                                                    value={values[`section${value.id_}`]? values[`section${value.id_}`].section :""}
                                                                                    
                                                                                    
                                                                                /></TableData>
                                                                            <TableData>
                                                                                <FastField 
                                                                                    as={Select}
                                                                                
                                                                                    {...getFieldProps(`section${value.id_}.languages`)}
                                                                                    name={`section${value.id_}.languages`}
                                                                                    value={values[`section${value.id_}`] ? values[`section${value.id_}`].languages:""}
                                                                                    className="bg-secondary p-1 rounded w-100"
                                                                                    >
                                                                                        {
                                                                                            options.map((value1,index)=>{
                                                                                                return (
                                                                                                    <option key={index} value={value1.value} label={value1.label}/>
                                                                                                )
                                                                                            })
                                                                                        }
                                                                                </FastField>
                                                                            </TableData>
                                                                            <TableData>
                                                                                
                                                                                            <Button width="fit-content" margin="2px" padding="0px" onClick = {async ()=>{
                                                                                                
                                                                                                setInitialdisplay((previousState)=>({
                                                                                                    ...previousState,
                                                                                                    
                                                                                                    [`displayon${value.id_}`]:"table-row",
                                                                                                    [`displayoff${value.id_}`]:"none"
                                                                                                
                                                                                            }))
                                                                                            delete initialState[`section${value.id_}`]
                                                                                                setInitialState((previousState)=>({
                                                                                                        ...initialState
                                                                                                }))
                                                                                                }
                                                                                            }
                                                                                            >
                                                                                                x
                                                                                            </Button>
                                                                                       
                                                                                            <Button width="fit-content" margin="2px" padding="0px" onClick={async ()=>{
                                                                                                    let responce = await fetch("/api/sectionapi",{
                                                                                                        method:"PUT",
                                                                                                        body:JSON.stringify(values[`section${value.id_}`])
                                                                                                    })
                                                                                                    setInitialdisplay((previousState)=>({
                                                                                                        ...previousState,
                                                                                                        [`displayon${value.id_}`]:"table-row",
                                                                                                        [`displayoff${value.id_}`]:"none"
                                                                                                    
                                                                                                }))
                                                                                                    // toast.success(`${JSON.stringify(responce)} was deleted`,{position: toast.POSITION.TOP_CENTER,autoClose:2000})
                                                                                                }}><BsXOctagonFill color="black" className="m-2"/>
                                                                                            </Button>
                                                                                        
                                                                            </TableData>
                                                                        </TableRow>
                                                                    </React.Fragment>
                                                                    
                                                                )
                                                            }):null
                                                        }
                                                        
                                                    </TableBody>
                                                </Table>
                                            </Form>
                                  )
                                }
                            }
                        </Formik>
                </Wrappper>
            {/* </Layout> */}
        </React.Fragment>
    )
}
