import React from "react"
import {StudentPersonalDetails} from "./studentPersonalDetails"
import {ReportCardTemplate} from "components/modules/Admin/ReportCard"
import Grid from "@material-ui/core/Grid"

import CloudDownloadIcon from '@material-ui/icons/CloudDownload'
import {PDFViewer,PDFDownloadLink} from "@react-pdf/renderer"

export const StudentDetails = ()=>{

    return(
        <React.Fragment>
        <StudentPersonalDetails />   
        <Grid item xs={12} sm={6} md={2}>
            <PDFDownloadLink 
                document={<ReportCardTemplate/>}
                fileName={"className"}
                >
                    {({ blob, url, loading, error }) => (loading ? 'Loading document...' : "<CloudDownloadIcon />")}
            </PDFDownloadLink>
        </Grid>
            <PDFViewer style={{width:"100%", height:"100vh"}}>
                <ReportCardTemplate />
            </PDFViewer>
        </React.Fragment>
    )
}