import styled from 'styled-components';

export const Input = styled.input`
  ${({width})=> width && `
    width :${width};
  `}
  
  box-sizing: border-box;
  border: solid black 1px;
  padding: 0.8rem 1rem;
  border-radius: 7px;
  margin-bottom: 0.5rem;
  transition: 0.3s;
  ${({theme})=> theme === "light" && `
    border:solid grey 2px;
  ` }
  ${({ error }) =>
    error &&
    `
        border: solid red 1px;
        color:red;
	`}

  &::placeholder {
    color: blue;
    
  }
`;
