let classOptions = [{
    id:1,
    class:"Form 1"
},
{
    id:2,
    class:"Form 2"
}]

let subjectOptions = [{
    id:1,
    subject:"History"
},
{
    id:2,
    subject:"Economy"
}]

export const SubjectToClass = ({AllSubject,AllClasss})=>{
    // let {data:subjectData,error:subjectError} = useSWR("/api/subjectapi")
    // let {data:classsData,error:classsError} = useSWR("/api/classesapi")
    
    // let subjectOptions = subjectData || AllSubject
    // let classOptions = classsData || AllClasss

     let initialValues = {
        
        subjectToClass:[{
            id:"",
            subject:"",
            classs:"",
            subjectCoefficient:"",
        }],
     }
    
    let validationSchema = Yup.object({
        subjectToClass: Yup.array().nullable().of(
            Yup.object({
                id: Yup.number().nullable().required("enter  ID"),
                subject: Yup.string().nullable().required("enter subject"),
                classs: Yup.string().nullable().required("enter class"),
                subjectCoefficient: Yup.number().nullable().required("Enter Coefficient").max(10,"most be < 1")
            })
        )
    })
    let onSubmit = async (values, onSubmitProps) =>{
        onSubmitProps.setSubmitting(true)
        
        
        
        let res = await fetch("/api/subjectClassapi",
        {
            method:"POST",
            body: JSON.stringify(values),
            headers:{
                cookies:"name"
            }
        })
    //    await toast.success("BasicConfiguration",{position: toast.POSITION.TOP_CENTER,autoClose:1000})
        onSubmitProps.resetForm(true)
        // trigger("/api/a")
    }
    let onReset = ()=>{
        // 
    }

    return (
        <React.Fragment>
                <Wrapper >
                    <CardTitle>
                        Assign Subject To Class
                    </CardTitle>

                    <Box>
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={onSubmit}
                        onReset={onReset}
                        >
                        {({
                                values,
                                errors,
                                touched,
                                isValid,
                                dirty,
                                handleChange,
                                handleBlur,
                                handleSubmit,
                                isSubmitting,
                                setFieldValue,
                                getFieldProps
                                /* and other goodies */
                            })=>(
                            <Form onSubmit={handleSubmit}>
                            <Card>
                                    <FieldArray name="subjectToClass">
                                        
                                        {   
                                            (fieldprops,index)=>{
                                                let {push,remove,form} = fieldprops
                                                const {values} = form
                                                const {subjectToClass} = values 
                                            return(
                                                <div key={index}>
                                                {
                                                    subjectToClass.map((value,index)=>{
                                                        console.log("valuess::",value)
                                                        return(
                                                                <Grid>
                                                                        <Container>
                                                                            <label  className="" >Class ID</label>
                                                                        </Container>
                                                                    
                                                                        <Container>
                                                                            <Field
                                                                                    error={(((errors || {})["subjectToClass"] || {})[index] ||{})["id"] && ((((touched || {})["subjectToClass"] || {})[index] ||{})["id"])  ? true : false }
                                                                                    label={((((errors || {})["subjectToClass"] || {})[index] ||{})["id"]) && ((((touched || {})["subjectToClass"] || {})[index] ||{})["id"]) ? errors["subjectToClass"][index]["id"] : "subject To Class ID" }
                                                                                    {...getFieldProps(subjectToClass[index].id)}
                                                                                    component={TextField}
                                                                                    type="number"
                                                                                    step="1"
                                                                                    value={value.id}
                                                                                    placeholder="subject To Class ID"
                                                                                    name={`subjectToClass[${index}].id`}
                                                                                    id={`subjectToClass[${index}].id`}
                                                                                    min="1"
                                                                                    variant="filled"
                                                                                />
                                                                        </Container>

                                                                        <Container>
                                                                            subject To Class
                                                                        </Container>
                                                                        <div style={{margin:"2% 0 2% 0"}}>
                                                                            <Field
                                                                                component={Autocomplete}
                                                                                // {...getFieldProps("Gender")}
                                                                                value={value.subject}
                                                                                // defaultValue={values.Title}
                                                                                onBlur={handleBlur}
                                                                                onChange={(e,selectedOption )=>
                                                                                    {setFieldValue(`subjectToClass[${index}].subject`, selectedOption)
                                                                                    //  setFieldError("Gender",errors.Gender)
                                                                                        }
                                                                                }
                                                                                
                                                                                // inputValue={values.Gender.title}
                                                                                options={subjectOptions}
                                                                                name={`subjectToClass[${index}].subject`}
                                                                                id={`subjectToClass[${index}].subject`}
                                                                                getOptionSelected = {(option,valuer)=> option.id == valuer.id}
                                                                                getOptionLabel={option => option.subject}
                                                                                renderInput={params => (
                                                                                    <>
                                                                                    
                                                                                    <Field 
                                                                                        component={TextField}
                                                                                        {...params}
                                                                                        name={`subjectToClass[${index}].subject`}
                                                                                        error={(((errors || {})["subjectToClass"] || {})[index] ||{})["subject"] && ((((touched || {})["subjectToClass"] || {})[index] ||{})["subject"])  ? true : false }
                                                                                        label={((((errors || {})["subjectToClass"] || {})[index] ||{})["subject"]) && ((((touched || {})["subjectToClass"] || {})[index] ||{})["subject"]) ? errors["subjectToClass"][index]["subject"] : "subject --> Class" }
                                                                                        variant="filled"    
                                                                                    
                                                                                    />
                                                                                    </>
                                                                                
                                                                                )}

                            
                                                                            />
                                                                        </div> 
                                                                        <Container>
                                                                            class
                                                                        </Container>
                                                                        <div style={{margin:"2% 0 2% 0"}}>
                                                                            <Field
                                                                                component={Autocomplete}
                                                                                // {...getFieldProps("Gender")}
                                                                                value={value.classs}
                                                                                // defaultValue={values.Title}
                                                                                onBlur={handleBlur}
                                                                                onChange={(e,selectedOption )=>
                                                                                    {setFieldValue(`subjectToClass[${index}].classs`, selectedOption)
                                                                                        }
                                                                                }
                                                                                
                                                                                // inputValue={values.Gender.title}
                                                                                options={classOptions}
                                                                                name={`subjectToClass[${index}].classs`}
                                                                                id={`subjectToClass[${index}].classs`}
                                                                                getOptionSelected = {(option,valuer)=> option.id == valuer.id}
                                                                                getOptionLabel={option => option.class}
                                                                                renderInput={params => (
                                                                                    <>
                                                                                    
                                                                                    <Field 
                                                                                        component={TextField}
                                                                                        {...params}
                                                                                        name={`subjectToClass[${index}].classs`}
                                                                                        error={(((errors || {})["subjectToClass"] || {})[index] ||{})["classs"] && ((((touched || {})["subjectToClass"] || {})[index] ||{})["classs"])  ? true : false }
                                                                                        label={((((errors || {})["subjectToClass"] || {})[index] ||{})["classs"]) && ((((touched || {})["subjectToClass"] || {})[index] ||{})["classs"]) ? errors["subjectToClass"][index]["classs"] : "class " }
                                                                                        variant="filled"    
                                                                                    
                                                                                    />
                                                                                    </>
                                                                                
                                                                                )}

                            
                                                                            />
                                                                        </div> 
                                                                        <Container>
                                                                            <label  className="" >coefficient</label>
                                                                        </Container>
                                                                    
                                                                        <Container>
                                                                            <Field
                                                                                    error={(((errors || {})["subjectToClass"] || {})[index] ||{})["subjectCoefficient"] && ((((touched || {})["subjectToClass"] || {})[index] ||{})["subjectCoefficient"])  ? true : false }
                                                                                    label={((((errors || {})["subjectToClass"] || {})[index] ||{})["subjectCoefficient"]) && ((((touched || {})["subjectToClass"] || {})[index] ||{})["subjectCoefficient"]) ? errors["subjectToClass"][index]["subjectCoefficient"] : "subject Coefficient" }
                                                                                    {...getFieldProps(subjectToClass[index].subjectCoefficient)}
                                                                                    component={TextField}
                                                                                    type="number"
                                                                                    step="1"
                                                                                    value={value.subjectCoefficient}
                                                                                    placeholder="subject Coefficient"
                                                                                    name={`subjectToClass[${index}].subjectCoefficient`}
                                                                                    id={`subjectToClass[${index}].subjectCoefficient`}
                                                                                    min="1"
                                                                                    variant="filled"
                                                                                />
                                                                        </Container>

                                                                    
                                                                    <Grid xs={2} md={4}>
                                                                    {index >= 1 ? <Button  backgroundColor="red" margin="2% 2% 2% 0" onClick={()=>remove(index)}>X</Button> :null}
                                                                        <Button margin="2% 2% 2% 0" type="Button" onClick={
                                                                            ()=>push({
                                                                                    id:"",
                                                                                    subject:"",
                                                                                    classs:"",
                                                                                })} disabled={!(dirty && isValid ) 
                                                                                }>+</Button>
                                                                    </Grid>
                                                                </Grid>
                                                                
                                                           
                                                        )
                                                    })
                                                }
                                                
                                                </div>
                                            )  
                                            }
                                        }
                                    </FieldArray>
                                
                                <div>
                                    
                                    {!(dirty && isValid ) ? <Button margin="2% 2% 0 0" disabled={!(dirty && isValid )} type="submit"  >submit</Button>:<Button margin="2% 2% 0 0"  type="submit" className="pl-5 pr-5 m-2 bg-danger" >submit</Button>}
                                    
                                    <Button margin="2% 2% 0 0" backgroundColor="yellow"  type="reset"  >reset</Button>
                                </div>  
                               
                            </Card>       
                            </Form>
                            
                        
                            )
}                   
                    </Formik>
                    </Box>
                </Wrapper>
        </React.Fragment>
    )
}


