import styled from "styled-components"

export const Grid = styled.div`
    display:grid;
    grid-template-columns:auto;
    margin:0px;
    
    ${({margin})=>margin && `
        margin:${margin};
    `}
    ${({backgroundColor})=>backgroundColor && `
        background-color:${backgroundColor};
        border-radius:10px;
    `}
    ${({border})=>border && `
        border:${border};
    `}
    ${({padding})=>padding && `
        padding:${padding};
    `}


    ${({xs,m_xs})=>xs  && `
        @media(min-width: 0px){
            grid-template-columns:repeat(${xs},${100/xs}%);
            margin:${m_xs};
        }
    `}
    ${({sm,m_sm})=>sm && `
        @media(min-width: 600px){
            grid-template-columns:repeat(${sm},${100/sm}%);
            margin:${m_sm};
        }
    `}
    ${({md,m_md})=>md && `
        @media(min-width: 950px){
            grid-template-columns:repeat(${md},${100/md}%);
            margin:${m_md};
        }
    `}
    ${({lg,m_lg})=>lg && `
        @media(min-width: 1100px){
            grid-template-columns:repeat(${lg},${100/lg}%);
            margin:${m_lg};
        }
    `}
    ${({xl,m_xl})=>xl && `
        @media(min-width: 1200px){
            grid-template-columns:repeat(${xl},${100/xl}%);
            margin:${m_xl};

        }
    `}
`